import React from "react";
import Layout from "../components/layout";
import SEO from "../components/seo";
import { graphql, Link } from "gatsby";
import MapChart from "../components/map";
import { useState, useEffect } from "react";
import GoogleContext from "../components/GoogleContext";

export default function FoodPage({
  data: {
    allMarkdownRemark: { edges },
  },
}) {
  const [google, setGoogle] = useState(null);

  useEffect(() => {
    if (!google) {
      if (window.google) {
        setGoogle(window.google);
      }
    }

    return () => {
      let script = document.getElementById("googleChartsScript");
      if (script) {
        script.remove();
      }
    };
  }, [google]);

  const plays = [
    ["Country", "Most memorable dish"],
    ["Romania", "Sarmale"],
    ["United Kingdom", "?"],
    ["United States", "Grits"],
    ["France", "Canele"],
    ["Switzerland", "Fondue"],
    ["Ireland", "?"],
    ["Germany", "Wurst"],
    ["Norway", "?"],
    ["Greece", "Dolmades"],
    ["Argentina", "Steak"],
    ["Brazil", "Steak"],
    ["South Korea", "Gogi Guk Su"],
    ["Japan","Ramen"],
    ["Thailand", "N/A"],
    ["China", "Xiao Long Bao"],
    ["India", "Kheer"],
    ["Laos", "Pla Pao Gluer"],
    ["Georgia", "?"],
    ["Sweden", "Meatballs"],
    ["Poland", "Pierogi"],
    ["Bulgaria", "Cucumber soup"],
    ["Hungary", "Goulash"],
    ["Ethiopia", "?"],
    ["Morocco", "Tagine"],
    ["Canada", "Poutine"],
    ["Mexico", "Burrito"],
    ["Belgium", "Chocolate"],
    ["Spain", "Paella"],
    ["Austria", "Strudel"],
    ["Turkey", "Baklava"],
    ["Nepal", "Momo"],
    ["Vietnam", "Pho"],
    ["Australia", "Fairy Bread"],
    ["Nigeria", "Smoked jollof rice"], 
    ["Cuba", "Cubano"],
    ["Russia", "Borscht"],
    ["Estonia", "?"],
    ["Egypt", "?"],
    ["Jamaica", "?"],
    ["South Africa", "Biltong"],
    ["Italy", "Tiramisu"],
    ["Peru", "Ceviche"],
  ];

  const posts = edges
    .map((edge) => edge?.node?.frontmatter)
    .filter((edge) => edge != null);

  return (
    <Layout currentPage="Interests" currentSubPage="Food">
      <SEO
        title="Food around the world"
        description={
          "A page dedicated to my love for food. I will move my recipes into here, as well as keep track of foods I've eaten from around the world."
        }
        keywords={[
          "Food",
          "World",
          "Map",
          "World Map",
          "Challenge",
          "Around The World",
          "Recipe",
        ]}
      />
      <GoogleContext.Provider value={{ google, setGoogle }}>
        <h2>Recipes</h2>
        <p>Coming Soon</p>
        {posts?.map((post, index) => (
          <div key={"food" + index} className="lcars-listing">
            <Link to={post.slug}>
              <div className="lcars-listing-inner">
                <div>★ {post.subcategory}</div>
                <div>{post.title}</div>
                <div>{post.date}</div>
              </div>
            </Link>
          </div>
        ))}
        
        <div>
          <h2>Food Around the World</h2>
          <p>
            <MapChart data={plays} id="play" />
          </p>
        </div>
      </GoogleContext.Provider>
    </Layout>
  );
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date] }
      filter: { frontmatter: { category: { eq: "food" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            slug
            title
            category
            subcategory
          }
        }
      }
    }
  }
`;
